/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionVertical, optionSize, EMPTY_INITIAL } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';

const Growth = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [growthData, setData] = useState(null);
  const [resultData, setResultData] = useState(null);
  const [selectedData, setSelectedData] = useState(null);
  const [selectedBubbleData, setSelectedBubbleData] = useState(null);
  const [idState, setReferenceID] = useState(null);
  const [bubbleName, setBubbleName] = useState(null);

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      }
    })
  }

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelMeasure();
    await onCancelRange();
    await onCancelVertical();
    await onCancelSize();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyMeasure();
    await onApplyRange();
    await onApplyVertical();
    await onApplySize();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0].key : ''
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio responsive for range values
  const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio responsive for vertical values
  const vertical_initial_value = optionVertical ? optionVertical[0].key : EMPTY_INITIAL
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val)
  }
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply)
  }

  // radio responsive for show by values
  const size_initial_value = optionSize ? optionSize[0].key : EMPTY_INITIAL
  const [size_val_onApply, set_size_val_onApply] = useState(size_initial_value);
  const [size_val, set_size_val] = useState(size_initial_value);
  const onApplySize = () => {
    set_size_val_onApply(size_val)
  }
  const onCancelSize = () => {
    set_size_val(size_val_onApply)
  }

  // Axios call
  const fetchData = async () => {
    const token = localStorage.getItem('token');
    setIsLoading(true);

    axios({
      method: 'GET',
      url: `${API_BASE}/orbis/api/customerrevenuegrowth?measure=${measure_val_onApply}&range=${range_val_onApply}&vertical=${vertical_val_onApply}&showby=${size_val_onApply}`,
      headers: {
        'Content-Type': 'application/json',
        "Authorization": `bearer ${token}`
      }
    }).then(result => {
      const data = result.data;
      const resultSelected = data.segment_ranking;

      setData(data);
      setResultData(resultSelected);
      setIsLoading(false);

      console.log(result.config.url)

    }).catch(error => {
      console.log(error);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, [measure_val_onApply, range_val_onApply, vertical_val_onApply, size_val_onApply]);

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              radioVal={measure_val}
              setRadioVal={set_measure_val}
              onApply={onApplyMeasure}
              onCancel={onCancelMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
              radioVal={range_val}
              setRadioVal={set_range_val}
              onApply={onApplyRange}
              onCancel={onCancelRange}
              initialValue={range_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              id="vertical"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionVertical}
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
              onApply={onApplyVertical}
              onCancel={onCancelVertical}
              initialValue={vertical_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Show By"
              id="size2"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionSize}
              radioVal={size_val}
              setRadioVal={set_size_val}
              onApply={onApplySize}
              onCancel={onCancelSize}
              initialValue={size_initial_value}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              visibleInModal
              radioVal={measure_val}
              setRadioVal={set_measure_val}
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
              radioVal={range_val}
              setRadioVal={set_range_val}
            />
            <ResponsiveRadioDD
              title="Vertical"
              id="vertical2"
              options={optionVertical}
              visibleInModal
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
            />
            <ResponsiveRadioDD
              title="Show By"
              id="size2"
              options={optionSize}
              visibleInModal
              radioVal={size_val}
              setRadioVal={set_size_val}
            />
          </FilterModal>
        </FilterContainer>
        < GrowthContent
          bubbleName={bubbleName}
          selectedData={selectedData}
          idState={idState}
          width={width}
          resultData={resultData}
          selectedBubbleData={selectedBubbleData}
          setSelectedData={setSelectedData}
          setSelectedBubbleData={setSelectedBubbleData}
          setBubbleName={setBubbleName}
          setReferenceID={setReferenceID}
          growthData={growthData}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Growth;
