import React, { useState } from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { EMPTY_INITIAL } from '../../constants/filterOptions';

import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import ResponsiveBasicDD from '../../components/customDropdown/ResponsiveBasicDD';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';

const stackedColumnOption = [
  {
    key: 'q_by_q_growth_percent',
    text: 'Show Q/Q Growth'
  },
  {
    key: 'y_by_y_growth_percent',
    text: 'Show Y/Y Growth'
  }
]

const OverviewContent = (props) => {
  const { segment_details, overall, overviewData, width, selectedRangeValue } = props;

  const stacked_column_initial_value = stackedColumnOption ? stackedColumnOption[0].key : EMPTY_INITIAL;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>;
  
    return (
      <>
        <span className={`indicator ${bgColor}`}> 
            {icon}
            {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
          {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
          {convertToPercent(cell)}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'revenue',
      text: 'REVENUE',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      formatter: formatToPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>customer revenue</Card.Title>
              <Kpi
                data={overall}
                total={overall.total}
                withPercent={true}
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>customer revenue by vertical</Card.Title>
              <div className="d-flex">
                <Legend legend={overviewData.customer_revenue_by_vertical ? overviewData.customer_revenue_by_vertical.legend : overviewData.customer_revenue_by_product.legend} />
                {
                  selectedRangeValue === "last_completed_quarter" ? 
                  <ResponsiveBasicDD
                    className=" ml-auto"
                    id="overviewStackedColumn"
                    wide={width >= 1516 ? true : false}
                    narrow={width < 1516 ? true : false}
                    options={stackedColumnOption}
                    value={stackedColumnSelection}
                    setValue={setStackedColumnSelection}
                  /> : null 
                }
              </div>
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="customer_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  stackedColumnSelection={stackedColumnSelection}
                  selectedRangeValue={selectedRangeValue}
                  data={overviewData.customer_revenue_by_vertical ? overviewData.customer_revenue_by_vertical.data : overviewData.customer_revenue_by_product.data}
                  legend={overviewData.customer_revenue_by_vertical ? overviewData.customer_revenue_by_vertical.legend : overviewData.customer_revenue_by_product.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>segment mrr mix</Card.Title>
              <Legend legend={overviewData.segment_revenue_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"customer_revenue_overview_donut_chart"}
                  totalTitle={"CUSTOMER REVENUE"}
                  total={overviewData.segment_revenue_mix.total_revenue}
                  data={overviewData.segment_revenue_mix.data}
                  legend={overviewData.segment_revenue_mix.legend}
                  isDollar={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details scrollable'
                data={segment_details === undefined ? [] : segment_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
         
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={overviewData.segment_trends_by_vertical ? overviewData.segment_trends_by_vertical.legend : overviewData.segment_trends_by_product.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"customer_revenue_overview_line_chart"}
                  data={overviewData.segment_trends_by_vertical ? overviewData.segment_trends_by_vertical.data : overviewData.segment_trends_by_product.data}
                  legend={overviewData.segment_trends_by_vertical ? overviewData.segment_trends_by_vertical.legend : overviewData.segment_trends_by_product.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default OverviewContent;