import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionAccounts, optionProspect, EMPTY_INITIAL } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import NavigatorContent from './NavigatorContent';

const Navigator = () => {
  const [navigatorData, setOverviewData] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  // Axios call
  const fetchData = () => {
    setIsLoading(true)
    axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/navigator.json`,
    }).then(result => {
      setOverviewData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const all_accounts = navigatorData ? navigatorData.all_accounts : [];
  const newly_added_accounts = navigatorData ? navigatorData.newly_added_accounts : [];
  const world_wide_map = navigatorData ? navigatorData.deployment_map : [];

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelAccounts()
    await onCancelProspects()
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyAccounts()
    await onApplyProspects()
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // radio responsive for accounts values
  const accounts_initial_value = optionAccounts ? optionAccounts[0].key : EMPTY_INITIAL
  const [accounts_val_onApply, set_accounts_val_onApply] = useState(accounts_initial_value);
  const [accounts_val, set_accounts_val] = useState(accounts_initial_value);
  const onApplyAccounts = () => {
    set_accounts_val_onApply(accounts_val)
  }
  const onCancelAccounts = () => {
    set_accounts_val(accounts_val_onApply)
  }

  // radio responsive for prospects values
  const prospects_initial_value = optionProspect ? optionProspect[0].key : EMPTY_INITIAL
  const [prospects_val_onApply, set_prospects_val_onApply] = useState(prospects_initial_value);
  const [prospects_val, set_prospects_val] = useState(prospects_initial_value);
  const onApplyProspects = () => {
    set_prospects_val_onApply(prospects_val)
  }
  const onCancelProspects = () => {
    set_prospects_val(prospects_val_onApply)
  }

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <ResponsiveRadioDD
              id="accounts"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionAccounts}
              radioVal={accounts_val}
              setRadioVal={set_accounts_val}
              onApply={onApplyAccounts}
              onCancel={onCancelAccounts}
              initialValue={accounts_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              id="include_only"
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionProspect}
              radioVal={prospects_val}
              setRadioVal={set_prospects_val}
              onApply={onApplyProspects}
              onCancel={onCancelProspects}
              initialValue={prospects_initial_value}
            />
          </div>

          <FilterModal
            title="Navigator"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <ResponsiveRadioDD
              id="accounts_modal"
              options={optionAccounts}
              visibleInModal
              radioVal={accounts_val}
              setRadioVal={set_accounts_val}
            />
            <ResponsiveRadioDD
              id="include_only_modal"
              options={optionProspect}
              visibleInModal
              radioVal={prospects_val}
              setRadioVal={set_prospects_val}
            />
          </FilterModal>
        </FilterContainer>

        {/* ============= Your Component here! ============= */}

        <NavigatorContent
          all_accounts={all_accounts}
          newly_added_accounts={newly_added_accounts}
          world_wide_map={world_wide_map}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />

    </>
  );
};

export default Navigator;
