const rowsPerPage = [{
  text: '10',
  value: 10
}, {
  text: '20',
  value: 20
}]

const defaultSorted = [
  {
    dataField: 'currency',
    order: 'desc', // or asc
  }
];

const columns = [
  {
    dataField: 'country',
    text: 'Deploy Country',
    sort: true,
  },
  {
    dataField: 'ibx',
    text: 'Deploy IBX',
    sort: true,
  },
  {
    dataField: 'currency',
    text: 'Constant Currency: MRC USD',
    sort: true,
  }
]

const BlankReportContent = () => {
  const [dragState, setDragState] = useState(draggableState);
  const [updateTable, setUpdateTable] = useState()
  const [isLoading, setLoading] = useState(false)

  // responsive dropdowns
  const dragRef = useRef(null);
  const { height } = useWidowResize(dragRef);
  const onRun = () => {
    setLoading(true)
    setTimeout(() => {
      setUpdateTable(columns);
      setLoading(false)
    }, 3000)
  }

  return (
    <>
      <div className="blank-report-page">
        <DraggableComponent
          buttonList={buttonList}
          grabableList={grabableList}
          dragState={dragState}
          setDragState={setDragState}
          horizontal
          dragRef={dragRef}
          maxHeight={height - 15}
        >
          <div className="preview-header">
            <Form.Group controlId="reportTitle">
              <Form.Label><GoPencil /></Form.Label>
              <Form.Control placeholder="Unnamed Report" />
            </Form.Group>
            <div className="btn-block">
              <Button variant="info" onClick={() => onRun()}>Run</Button>
              <Button variant="info">Save</Button>
              <Button variant="info">Download</Button>
            </div>
          </div>
          <div className="preview-area">
            {isLoading ? <Loader />
              : <>
                {updateTable ?
                  <CustomTable
                    tableName='preview-table'
                    data={dataTable !== undefined ? dataTable : []}
                    defaultSorted={defaultSorted}
                    columns={columns}
                    addPaginate
                    rowsPerPage={rowsPerPage}
                  />
                  : <p className="empty-fields">To generate a report, choose fields from the Field list on the left and click Run</p>
                }
              </>
            }

          </div>
        </DraggableComponent>
      </div>
    </>
  );
}

export default BlankReportContent;
