import React, { useEffect, useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi';
import './customDropdown.scss';


const ResponsiveRadioDD = ({ 
  title, 
  options, 
  wide, 
  narrow, 
  className, 
  id, 
  addTitleOnWide, 
  visibleInModal,
  radioVal,
  setRadioVal,
  onApply,
  onCancel,
  initialValue
}) => {
  const [show, setShow] = useState(false);
  const [wideClick, steClick] = useState(false)

  const handleSelect = (e) => {
    setRadioVal(e)
  }

  const handleToggle = () => {
    setShow(!show)
    onCancel();
  }

  function humanize(str) {
    if (str.indexOf('_') > -1){
      var i, frags = str.split('_');
      for (i=0; i<frags.length; i++) {
        frags[i] = frags[i].charAt(0).toUpperCase() + frags[i].slice(1);
      }
      return frags.join(' ');
    } else{
      return str.charAt(0).toUpperCase() + str.slice(1);
    }
  }

  useEffect(()=> {
   if(wideClick){
    onApply()
    steClick(false)
   } 
  }, [onApply, wideClick])

  return (
    <>
      {visibleInModal
        ? <div className={`radio-in-modal narrow-list filter-modal ${className ? className : ''}`}>
          <div className="title">{title ? title : radioVal}</div>
          <Form>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='radio'
                id={`radio_modal-${option.key}`}
                checked={radioVal === option.key}
                label={option.text}
                onChange={() => handleSelect(option.key)}
              />
            ))}
          </Form>
        </div>
        : <>
          {wide &&
            <ul className={`responsive-radio-dd wide-list ${className ? className : ''}`}>
              {options
                ? <>
                  {options.map(option => (
                    <li key={option.key}
                      onClick={() => {
                        handleSelect(option.key);
                        steClick(true);
                      }}
                      className={`${option.key === radioVal ? 'active' : ''}`}
                    >
                      {addTitleOnWide ? `${title ? title : ''} ` : ''}{option.text}
                    </li>
                  ))}
                </>
                : <li>{initialValue}</li>
              }
            </ul>
          }

          {narrow &&
            <Dropdown
              show={show}
              onToggle={handleToggle}
              className={`responsive-radio-dd narrow-list ${className ? className : ''}`}
            >
              <Dropdown.Toggle id={`responsive_radio_dd-${id}`} variant='light'>
                {title ? title : (radioVal ? humanize(radioVal) : '')} <BiChevronDown viewBox="6 8 12 12" />
              </Dropdown.Toggle>

              <Dropdown.Menu show={show}>
                {options && options.map(option => (
                  <Form.Check
                    key={option.key}
                    custom
                    type='radio'
                    id={`raido_res-${option.key}`}
                    checked={radioVal === option.key}
                    label={option.text}
                    onChange={() => handleSelect(option.key)}
                  />
                ))}
                <div className="btn-block">
                  <Button variant="link" onClick={() => handleToggle()}>Cancel</Button>
                  <Button variant="primary" onClick={() => {
                    onApply(); 
                    setShow(!show);
                  }}>Apply</Button>
                </div>
              </Dropdown.Menu>
            </Dropdown>
          }
        </>
      }
    </>
  );
}

export default ResponsiveRadioDD;
