/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionRegion, optionMetro, EMPTY_INITIAL, optionVertical, optionGroup2 } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator';
import RadioDD from '../../components/customDropdown/RadioDD';
import CheckboxDD from '../../components/customDropdown/CheckboxDD';
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import OverviewContent from './OverviewContent';

const Overview = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [overviewData, setData] = useState(null);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelVertical();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelGroup1();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyVertical();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyGroup1();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val, set_region_val] = useState([]);
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val)
  }
  const onCancelRegion = () => {
    set_region_val(region_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val, set_metro_val] = useState([]);
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0].key : ''
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio responsive for range values
  const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio responsive for vertical values
  const vertical_initial_value = optionVertical ? optionVertical[0].key : EMPTY_INITIAL
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val)
  }
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply)
  }

  // radio responsive for group2 values
  const group2_initial_value = optionGroup2 ? optionGroup2[0].key : EMPTY_INITIAL
  const [group2_val_onApply, set_group2_val_onApply] = useState(group2_initial_value);
  const [group2_val, set_group2_val] = useState(group2_initial_value);
  const onApplyGroup1 = () => {
    set_group2_val_onApply(group2_val)
  }
  const onCancelGroup1 = () => {
    set_group2_val(group2_val_onApply)
  }

  const segment_details = overviewData ? overviewData.segment_details : [];
  const overall = overviewData ? overviewData.overall : [];

  // Axios call
  const fetchData = () => {
    const token = localStorage.getItem('token');
    setIsLoading(true);

    axios({
      method: 'GET',
      url: `${API_BASE}/orbis/api/customerrevenueoverview?measure=${measure_val_onApply}&range=${range_val_onApply}&vertical=${vertical_val_onApply}&groupby=${group2_val_onApply}${region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : ''}${metro_val.length > 0 ? `&metro=${metro_val_onApply.join(",").toString()}` : ''}`,
      headers: {
        'Content-Type': 'application/json',
        "Authorization": `bearer ${token}`
      }
    }).then(result => {
      console.log(result.config.url)
      setData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setIsLoading(false);
    }); 
  };

  useEffect(() => {
    fetchData();
  }, [region_val_onApply, metro_val_onApply, measure_val_onApply, range_val_onApply, vertical_val_onApply, group2_val_onApply]);

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              checkVal={region_val}
              setCheckVal={set_region_val}
              onApply={onApplyRegion}
              onCancel={onCancelRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              checkVal={metro_val}
              setCheckVal={set_metro_val}
              onApply={onApplyMetro}
              onCancel={onCancelMetro}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              radioVal={measure_val}
              setRadioVal={set_measure_val}
              onApply={onApplyMeasure}
              onCancel={onCancelMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
              radioVal={range_val}
              setRadioVal={set_range_val}
              onApply={onApplyRange}
              onCancel={onCancelRange}
              initialValue={range_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              id="vertical"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionVertical}
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
              onApply={onApplyVertical}
              onCancel={onCancelVertical}
              initialValue={vertical_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionGroup2}
              radioVal={group2_val}
              setRadioVal={set_group2_val}
              onApply={onApplyGroup1}
              onCancel={onCancelGroup1}
              initialValue={group2_initial_value}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              visibleInModal
              checkVal={region_val}
              setCheckVal={set_region_val}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              visibleInModal
              checkVal={metro_val}
              setCheckVal={set_metro_val}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              visibleInModal
              radioVal={measure_val}
              setRadioVal={set_measure_val}
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
              radioVal={range_val}
              setRadioVal={set_range_val}
            />
            <ResponsiveRadioDD
              title="Vertical"
              id="vertical2"
              options={optionVertical}
              visibleInModal
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
            />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              options={optionGroup2}
              visibleInModal
              radioVal={group2_val}
              setRadioVal={set_group2_val}
            />
          </FilterModal>
        </FilterContainer>

        <OverviewContent
          segment_details={segment_details}
          overall={overall}
          overviewData={overviewData}
          width={width}
          selectedRangeValue={range_val_onApply}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Overview;
