import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup1, optionRegion, optionMetro, EMPTY_INITIAL } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import DeploymentContent from './DeploymentContent';

const Deployments = () => {
  const [deploymentData, setDeploymentData] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  // Axios call
  const fetchData = () => {
    setIsLoading(true)
    axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/deployments.json`,
    }).then(result => {
      setDeploymentData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }

  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelGroup1();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyGroup1();
    setShowFilter(false);
  }
  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val)
  }
  const onCancelRegion = () => {
    set_region_val(region_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0].key : ''
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio responsive for range values
  const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio responsive for group1 values
  const group1_initial_value = optionGroup1 ? optionGroup1[0].key : EMPTY_INITIAL
  const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
  const [group1_val, set_group1_val] = useState(group1_initial_value);
  const onApplyGroup1 = () => {
    set_group1_val_onApply(group1_val)
  }
  const onCancelGroup1 = () => {
    set_group1_val(group1_val_onApply)
  }

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  console.log(deploymentData)
  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              checkVal={region_val}
              setCheckVal={set_region_val}
              onApply={onApplyRegion}
              onCancel={onCancelRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              checkVal={metro_val}
              setCheckVal={set_metro_val}
              onApply={onApplyMetro}
              onCancel={onCancelMetro}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              radioVal={measure_val}
              setRadioVal={set_measure_val}
              onApply={onApplyMeasure}
              onCancel={onCancelMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
              radioVal={range_val}
              setRadioVal={set_range_val}
              onApply={onApplyRange}
              onCancel={onCancelRange}
              initialValue={range_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionGroup1}
              radioVal={group1_val}
              setRadioVal={set_group1_val}
              onApply={onApplyGroup1}
              onCancel={onCancelGroup1}
              initialValue={group1_initial_value}
            />

          </div>
          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              visibleInModal
              checkVal={region_val}
              setCheckVal={set_region_val}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              visibleInModal
              checkVal={metro_val}
              setCheckVal={set_metro_val}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              visibleInModal
              radioVal={measure_val}
              setRadioVal={set_measure_val}
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
              radioVal={range_val}
              setRadioVal={set_range_val}
            />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              options={optionGroup1}
              visibleInModal
              radioVal={group1_val}
              setRadioVal={set_group1_val}
            />
          </FilterModal>
        </FilterContainer>

        <DeploymentContent deploymentData={deploymentData[0]} />

      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Deployments;
