import React, { useRef, useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { useWidowResize } from '../../constants/utilityHooks';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { Row, Col, Card, Button } from 'react-bootstrap';
import { BiExpand } from "react-icons/bi";
import { RiDragMove2Line, RiDeleteBin6Line } from "react-icons/ri";
import { GrAdd } from "react-icons/gr";


import arrayMove from 'array-move';
import cuid from 'cuid';
import axios from 'axios';

//components
import KPI from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
//charts
import StackedColumnChart from '../../components/chart/StackedColumnChart';
//modal
import AddMetricsModal from './AddMetricsModal';
//common components
import Loader from '../../components/loader/Loader';
// constant
import { API_BASE_URL } from '../../constants/config';
//style
import './sortableComponent.scss';

const SortableComponent = (props) => {
  let history = useHistory();
  let [test, setTestItems] = useState([]);
  const [modalShow, setModalShow] = useState(false);
  const [content, setContent] = useState(false);
  const [isLoading, setIsLoading] = useState(false)

  const fetchPretty = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/executive-cockpit.json`,
    }).then(result => {
      if (result.data) {
        setTestItems(result.data.all);
        setIsLoading(false)
      }
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
  }, []);

  if (test) {
    test = test.map((val, index) => ({
      title: val.title,
      index: index,
      id: cuid(),
      url: val.url,
      chart: val.chart,
      overall: val.overall
    }));

    var found = false;
    for (var i = 0; i < test.length; i++) {
      if (test[i].label === 'add') {
        found = true;
        break;
      }
    }
    console.log('found', found)
    if (!found) {
      test.push({
        title: 'Add Metro',
        id: cuid(),
        index: test.length,
        label: 'add',
      });
    }
  }

  const goTo = (url) => {
    history.push('/executive-cockpit' + url);
  }

  const onSortEnd = ({ oldIndex, newIndex }) => {
    var filtered = test.filter(function (el) { return el.label != "add"; });
    setTestItems(arrayMove(filtered, oldIndex, newIndex))
  };

  const SortableItem = SortableElement(({ item }) => (
    <div className="item">
      <div className="inner-item">
        {item.label === 'add' ?
          <Card className="icon-center">
            <Card.Body>
              <Button className="btn-add"
                onClick={() => {
                  setModalShow(true);
                }}><GrAdd /></Button>
            </Card.Body>
          </Card>
          :
          <Card>
            <Card.Body>
              <Card.Title>{item.title}</Card.Title>
              <div className="button-container">
                <Button className="btn-gray"><RiDeleteBin6Line /></Button>
                {/* <Button className="btn-gray"><RiDragMove2Line /></Button> */}
                <Button className="btn-gray" onClick={() => goTo(item.url)}><BiExpand /></Button>
              </div>
              <Row>
                <Col>
                  {item.overall ?
                    <KPI data={item.overall}
                      total={item.overall.total}
                      withPercent={true} />
                    : null}
                </Col>
                <Col>
                  {item.chart ?
                    <Legend legend={item.chart.legend} />
                    : null}

                  {item.chart ?
                    <div style={{ height: "320px" }}>
                      <StackedColumnChart
                        id={item.id}
                        hiddenValueAxis={true}
                        isOneHundredPercent={false}
                        isPercentage={false}
                        largerWidth={true}
                        data={item.chart.data}
                        legend={item.chart.legend}
                        hideLabelBullet={true}
                      />
                    </div>
                    : null}

                </Col>
              </Row>
            </Card.Body>
          </Card>
        }
      </div >
    </div >
  ));

  const SortableList = SortableContainer(({ items }) => (
    <div className="container">
      {items.map((item, index) => (
        <SortableItem
          key={`${item.id}`}
          index={index}
          item={item}
          className={'sortable-item'}
        />
      ))}
    </div>
  ))
  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  return (
    <div className="SortableCards" ref={cardRef}>
      <AddMetricsModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        setContent={false}
      />
      {test ?
        <SortableList
          items={test}
          onSortEnd={onSortEnd}
          axis="xy"
          helperClass="SortableHelper"
        />
        : null}
    </div>

  );
}

export default SortableComponent;
// ReactDOM.render(<SortableComponent />, document.getElementById('root'));
