import React from 'react';

import { ReactComponent as NotifIcon } from '../../assets/icons/notification.svg';
import { ReactComponent as SearchIcon } from '../../assets/icons/search.svg';
import sampleAvatar from '../../assets/images/user-girl.jpg'; // To use as a sample avatar image only

import './topbar.scss';

let img_girl = 'https://media-exp1.licdn.com/dms/image/C5603AQEhJRwccbmylw/profile-displayphoto-shrink_400_400/0?e=1608768000&v=beta&t=T0BSG9QtXL53PMNPQUEENdtLEloneWHzW8EOMy5ngms'

const Topbar = () => {
    return (
        <div className="topbar">
            <nav className="nav-menu">
                <div className="search-bar">
                    <input id="global_search_bar" type="text" placeholder="Search for anything..." maxLength="50" />
                    <span className="icon">
                        <SearchIcon />
                    </span>
                </div>
                <div id="btn_show_notif" className="notif">
                    <NotifIcon />
                </div>
                <div className="avatar">
                    <img src={img_girl} alt="User avatar" />
                </div>
            </nav>
        </div>
    );
}

export default Topbar;