import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';

import './customerRevenue.scss';

const MetroModal = (props) => {
  const data = props.data;
  const selectedMetro = props.selectedMetro;
  let overall, customer_revenue_by_revenue_type,
    vertical_details, vertical_revenue_mix, metro;
  if (data) {
    overall = data.overall;
    customer_revenue_by_revenue_type = data.customer_revenue_by_vertical;
    vertical_details = data.vertical_details;
    vertical_revenue_mix = data.vertical_revenue_mix;
    metro = data.metro;
  }

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />

    );
  }


  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'mrr_revenue_mix',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment', // For Ranks
      text: 'VERTICAL',
      align: 'left',
      headerAlign: 'left',
      classes: 'vertical',
      headerClasses: 'vertical',
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mrr_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'sparkline_data',
      text: '',
      align: 'right',
      headerAlign: 'right',
      classes: 'sparkline-td',
      headerClasses: 'sparkline-td',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_total_usd_growth',
      text: '% TOTAL $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    }
  ]
  return (
    <Modal id="metro-modal" className="metro-modal"
      {...props}
      // size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {selectedMetro} customer revenue
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col className="fix-width" style={{ paddingRight: "30px" }}>
                <Kpi
                  data={overall}
                  total={overall.revenue}
                  withPercent={true}
                />
              </Col>
              <Col md={6} xs={12}>
                <Legend legend={customer_revenue_by_revenue_type.legend} />
                <div style={{ height: "350px" }}>
                  <StackedColumnChart
                    id="customer_revenue_metro_modal_by_revenue_type_stacked_column_chart"
                    largerWidth={true}
                    hiddenValueAxis={false}
                    isOneHundredPercent={false}
                    isPercentage={false}
                    hideLabelBullet={false}
                    data={customer_revenue_by_revenue_type.data}
                    legend={customer_revenue_by_revenue_type.legend}
                  />
                </div>
              </Col>
              <Col style={{ paddingLeft: "30px" }}>
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"customer_revenue_metro_modal_donut_chart"}
                    totalTitle={"CUSTOMER REVENUE"}
                    total={vertical_revenue_mix.customer_revenue}
                    data={vertical_revenue_mix.data}
                    legend={vertical_revenue_mix.legend}
                    isDollar={true}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <CustomTable
                  tableName='metro-vertical-details scrollable'
                  data={vertical_details === undefined ? [] : vertical_details}
                  defaultSorted={defaultSorted} // For Initial Sort ===
                  columns={columns}
                />
              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default MetroModal;