import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Modal, Button, Alert } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactComponent as LoadingIcon } from '../../assets/icons/loader.svg';
import { ReactComponent as Dollar } from '../../assets/icons/dollar_target.svg';
import { ReactComponent as Shakehands } from '../../assets/icons/shakehands.svg';
import { ReactComponent as NoEmail } from '../../assets/icons/no_email.svg';
import { ReactComponent as Server } from '../../assets/icons/server.svg';
import { ReactComponent as Team } from '../../assets/icons/team.svg';
import { ReactComponent as Tabular } from '../../assets/icons/report_tabular.svg';
import { ReactComponent as Summary } from '../../assets/icons/report_summary.svg';
import { ReactComponent as Matrix } from '../../assets/icons/report_matrix.svg';
import './dataMart.scss';
import { v4 as uuidv4 } from 'uuid';

import { buttonList } from '../../constants/datamartVariables';
import DraggableComponent from './DraggableComponent';
import CustomTable from '../../components/customTable/CustomTable';


const draggableState = {
  [uuidv4()]: {
    name: "COLUMNS",
    text: "Categories or Measures",
    items: []
  },
  [uuidv4()]: {
    name: "Grouping",
    text: "Categories only",
    items: []
  },
  [uuidv4()]: {
    name: "Filters",
    text: "Categories or Measures",
    items: []
  },
  [uuidv4()]: {
    name: "Values",
    text: "Measures only",
    items: []
  }
};

// animate
const animateStep = {
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: .5, bounce: 0 }
  },
  backEnter: {
    opacity: 0,
    x: '-100px',
    transition: { duration: .5, bounce: 0 }
  },
  backExit: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextEnter: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextExit: {
    opacity: 0,
    x: '-100px',
    transition: { duration: .5, bounce: 0 }
  }
}

const animateGenerate = {
  hidden: {
    opacity: 0
  },
  visible: {
    opacity: 1
  }
}

// table
const dataTable = [
  {
    country: 'United States',
    ibx: 'AT2',
    currency: '$7,796,359.00 '
  },
  {
    country: 'United States',
    ibx: 'AT3',
    currency: '$6,573,521.00 '
  },
  {
    country: 'United States',
    ibx: 'AT5',
    currency: '$3,973,020.00 '
  },
  {
    country: 'United States',
    ibx: 'BO1',
    currency: '$6,219,727.00 '
  },
  {
    country: 'United States',
    ibx: 'BO2',
    currency: '$1,364,924.00 '
  },
  {
    country: 'United States',
    ibx: 'CH1',
    currency: '$801,670.00 '
  },
  {
    country: 'United States',
    ibx: 'CH2',
    currency: '$7,891,213.00 '
  },
  {
    country: 'United States',
    ibx: 'CH3',
    currency: '$2,178,090.00 '
  },
  {
    country: 'United States',
    ibx: 'CH4',
    currency: '$7,793,514.00 '
  },
  {
    country: 'United States',
    ibx: 'CH7',
    currency: '$4,202,546.00'
  },
]

const defaultSorted = [
  {
    dataField: 'currency',
    order: 'desc', // or asc
  }
];

const columns = [
  {
    dataField: 'country',
    text: 'Deploy Country',
    sort: true,
  },
  {
    dataField: 'ibx',
    text: 'Deploy IBX',
    sort: true,
  },
  {
    dataField: 'currency',
    text: 'Constant Currency: MRC USD',
    sort: true,
  }
]

// loop
const step1List = [
  {
    icon: <Dollar className="icon" />,
    key: 'global_report',
    label: 'CAMPAIGN TO OPPORTUNITY',
  },
  {
    icon: <Shakehands className="icon" />,
    key: 'regional_report',
    label: 'OPPORTUNITY TO CASH',
  },
  {
    icon: <NoEmail className="icon" />,
    key: 'country_report',
    label: 'ISSUE TO RESOLUTION',
  },
  {
    icon: <Server className="icon" />,
    key: 'metro_report',
    label: 'CAPACITY MANAGEMENT',
  },
  {
    icon: <Team className="icon" />,
    key: 'sales_team_report',
    label: 'PEOPLE',
  }
]

const step2List = [
  {
    icon: <Tabular className="icon" />,
    key: 'tabular',
    label: 'TABULAR',
    desc: 'Similar to a spreadsheet, they consist simply of an ordered set of fields in columns, with each matching record listed in a row.',
  },
  {
    icon: <Summary className="icon" />,
    key: 'summary',
    label: 'SUMMARY',
    desc: 'Summary reports are similar to tabular reports, but also allow users to group rows of data, view subtotals.',
  },
  {
    icon: <Matrix className="icon" />,
    key: 'matrix',
    label: 'MATRIX',
    desc: 'Matrix reports are similar to summary reports but allow you to group and summarize data by both rows and columns.',
  },
]

const step4List = [
  {
    key: 'last_completed_quarter',
    label: 'LAST COMPLETED QUARTER',
  },
  {
    key: 'last_year',
    label: 'LAST YEAR',
  },
  {
    key: 'year_to_date',
    label: 'YEAR-TO-DATE',
  },
  {
    key: 'last_4_quarters',
    label: 'LAST 4 QUARTERS',
  },
  {
    key: 'custom',
    label: 'CUSTOM',
  },
]

// state
const initialState = {
  step1: '',
  step2: '',
  step3: '',
  step4: []
}

const GeneratorModal = (props) => {
  const { showGeneratorModal, onCancelModal } = props;
  let history = useHistory();
  const [stepOpen, setStepOpen] = useState(1);
  const [clickBack, setClickBack] = useState(false);
  const [clickNext, setClickNext] = useState(false);
  const [generating, setGenerating] = useState(false);
  const [stepsSelectedValue, setStepsSelectedValue] = useState(initialState)
  const [showValidate, setShowValidate] = useState(false);

  const onSelectValueStep1 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step1: e.target.id,
    })
  }

  const onSelectValueStep2 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step2: e.target.id,
    })
  }

  const onSelectValueStep4 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step3: e.target.id,
    })
  }

  const [dragState, setDragState] = useState(draggableState)

  const onCancel = async () => {
    await setClickBack(false)
    await setClickNext(false)
    await onCancelModal()
    setStepOpen(1)
    setStepsSelectedValue(initialState)
    setShowValidate(false)
  }

  const onBack = async () => {
    await setClickNext(false)
    await setClickBack(true)
    setStepOpen(stepOpen - 1)
  }

  const onNext = async () => {
    await setClickBack(false)
    await setClickNext(true)
    if(stepOpen === 1){
      if(stepsSelectedValue.step1 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
    if(stepOpen === 2){
      if(stepsSelectedValue.step2 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
    if(stepOpen === 3){
      setStepOpen(stepOpen + 1)
    }
    if(stepOpen === 4){
      if(stepsSelectedValue.step4 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
  }

  const runReport = () => {
    setGenerating(true);
    setStepOpen(6)
    // API here
    setTimeout(async () => {
      setGenerating(false)
    }, 5000)
  }

  const initialAnimate = clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false;
  const exitAnimate = clickBack ? 'backExit' : clickNext ? 'nextExit' : false;

  const viewReport = () => {
    history.push('/sef-service-rerport/blank-report/generated-report')
  }

  return (
    <>
      <Modal
        show={showGeneratorModal}
        onHide={onCancel}
        className="generator-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">
          <AnimatePresence exitBeforeEnter>
            {stepOpen === 1 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate='nextExit'
                modalTitle='STEP 1: CHOOSE A DATA UNIVERSE'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step1List && step1List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep1(e)}
                          id={item.key}
                          className={stepsSelectedValue.step1 === item.key ? 'active' : ''}
                        >
                          {item.icon}{item.label}
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 2 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 2: SELECT TABLE FORMAT'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step2List && step2List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep2(e)}
                          id={item.key}
                          className={stepsSelectedValue.step2 === item.key ? 'active' : ''}
                        >
                          {item.icon}{item.label} <span className="desc">{item.desc}</span> 
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 3 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 3: SELECT GLOBAL FILTER FIELDS'
              >
                <Modal.Body>
                  <DraggableComponent 
                    buttonList={buttonList}
                    dragState={dragState}
                    setDragState={setDragState}
                  />
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 4 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 4: SET GLOBAL FILTERS'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                    {step4List && step4List.map((item) => {
                      return (
                        <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                          <Button
                            variant="link"
                            onClick={(e) => onSelectValueStep4(e)}
                            id={item.key}
                            className={stepsSelectedValue.step3 === item.key ? 'active' : ''}
                          >
                            {item.label}
                          </Button>
                        </div>
                      )
                    })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 5 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 5: PREVIEW'
              >
                <Modal.Body>
                  <CustomTable
                    tableName='preview-table'
                    data={dataTable !== undefined ? dataTable : []}
                    defaultSorted={defaultSorted}
                    columns={columns}
                    addPaginate
                  />
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => runReport()}>Run Report</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 6 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
              >
                <Modal.Body>
                  {generating
                    ? <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <LoadingIcon className="icon mb-4" />
                      <h5 className="title">Generating Report</h5>
                      <p className="note">Well send the report to your email after we finish.</p>
                    </motion.div>
                    : <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <h5 className="title">Finished!</h5>
                      <Button variant="info" onClick={() => viewReport()}>View Report</Button>
                    </motion.div>
                  }
                </Modal.Body>
              </ContentContainer>
            )}
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  stepOpen,
  initialAnimate,
  exitAnimate,
  modalTitle,
  children,
}) => {
  return (
    <motion.div className={`content-container step-${stepOpen}`}
      key={stepOpen}
      variants={animateStep}
      initial={initialAnimate}
      animate='visible'
      exit={exitAnimate}
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default GeneratorModal;