import React from 'react';
import { Modal, Row, Col, Table } from 'react-bootstrap';
import CustomTable from '../../components/customTable/CustomTable';
import sampleFlagImage from '../../assets/images/sample-flag.png';
import MapModalChartNavigator from '../../components/chart/MapModalChartNavigator';
import Legend from '../../components/legend/Legend';

import './accountNavigator.scss';

const MatchmakerModal = (props) => {
  const data = props ? props.data : [];
  const overall = data ? data.overall : [];
  const metro_list = data ? data.metro_list : [];
  const matchmaker_map = data ? data.matchmaker_map : [];
  const matchmaker_legend = data ? data.matchmaker_map.legend : [];
  const title = props.title;

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt="sample flag" />
        <span className="body-name">{cell.name}</span>
      </>

    )
  }

  const defaultSorted = [
    {
      dataField: 'metro',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metro
    },
    {
      dataField: 'partner_deployed',
      text: 'Partners Deployed',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'interconnection',
      text: 'Interconnection',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  return (
    <Modal id="metro-modal" className="metro-modal"
      {...props}
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {title} opportunities
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Row>
          <Col xl={8}>
            <Table responsive className="metro-table-modal scrollable">
              < thead >
                <tr>
                  <th >Metro Presence</th>
                  <th >Partner Present but not Connected</th>
                  <th >Connected to Partner</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  {Object.keys(overall).map((key, i) => {
                    return (
                      <>
                        <td >{overall[key]}</td>
                      </>
                    )
                  })}
                </tr>
              </tbody>
            </Table>
            {matchmaker_legend ?
              <Legend legend={matchmaker_legend} />
              : null}
            <div style={{ height: "500px ", paddingTop: "2rem" }}>
              <MapModalChartNavigator
                id={"matchmaker_map_chart_modal"}
                largerWidth={false}
                data={matchmaker_map.data}
                isMatchmaker={true}
              />
            </div>
          </Col>
          <Col xl={4}>
            {metro_list ?
              <CustomTable
                tableName='metro-details scrollable'
                data={metro_list === undefined ? [] : metro_list}
                defaultSorted={defaultSorted}
                columns={columns}
              />
              : null}
          </Col>
        </Row>
      </Modal.Body >
    </Modal >
  );
}

export default MatchmakerModal;