/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionRanking, optionVertical2, optionRegion, optionMetro, optionHyperscaler, EMPTY_INITIAL } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import ShowTopBottomDD from '../../components/customDropdown/ShowTopBottomDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL, API_BASE } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import CustomerContent from './CustomerContent';

const Customer = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [customerData, setData] = useState()
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelHyperscaler();
    await onCancelShowTopBottom();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyHyperscaler();
    await onApplyShowTopBottom();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val)
  }
  const onCancelRegion = () => {
    set_region_val(region_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0].key : ''
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio for vertical values
  const vertical_initial_value = optionVertical2 ? optionVertical2[0].key : ''
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val)
  }
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply)
  }

  // radio responsive for range values
  const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio responsive for group1 values
  const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0].key : EMPTY_INITIAL
  const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
  const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
  const onApplyHyperscaler = () => {
    set_hyperscaler_val_onApply(hyperscaler_val)
  }
  const onCancelHyperscaler = () => {
    set_hyperscaler_val(hyperscaler_val_onApply)
  }

  // for show top or bottom ranking
  const [showTopBottomVal_onApply, setTopBottomVal_onApply] = useState({
    topBottom: 'radio_top',
    numberInput: 10,
    rankingPercentile: 'radio_ranking'
  });
  const [showTopBottomVal, setTopBottomVal] = useState({
    topBottom: 'radio_top',
    numberInput: 10,
    rankingPercentile: 'radio_ranking'
  });
  const onApplyShowTopBottom = () => {
    setTopBottomVal_onApply(showTopBottomVal)
  }
  const onCancelShowTopBottom = () => {
    setTopBottomVal(showTopBottomVal_onApply)
  }

  // Axios call
  const fetchData = () => {
    const token = localStorage.getItem('token');
    setIsLoading(true);
  
    axios({
      method: 'GET',
      url: `${API_BASE}/orbis/api/customerrevenuecustomer?measure=${measure_val_onApply}&range=${range_val_onApply}&vertical=${vertical_val_onApply}&hyperscaler=${hyperscaler_val_onApply}&show=${showTopBottomVal_onApply.topBottom.split('_')[1]}&showvalue=${showTopBottomVal_onApply.numberInput}${region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : ''}${metro_val_onApply.length > 0 ? `&rmetro=${metro_val_onApply.join(",").toString()}` : ''}`,
      headers: {
        'Content-Type': 'application/json',
        "Authorization": `bearer ${token}`
      }
    }).then(result => {
      setData(result.data);
      setIsLoading(false);

      console.log(result.config.url)

    }).catch(error => {
      console.log(error);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, [measure_val_onApply, range_val_onApply, vertical_val_onApply, hyperscaler_val_onApply, region_val_onApply, metro_val_onApply, showTopBottomVal_onApply]);
  
  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              checkVal={region_val}
              setCheckVal={set_region_val}
              onApply={onApplyRegion}
              onCancel={onCancelRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              checkVal={metro_val}
              setCheckVal={set_metro_val}
              onApply={onApplyMetro}
              onCancel={onCancelMetro}
            />
            <RadioDD
              title="Vertical"
              id="vertical"
              options={optionVertical2}
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
              onApply={onApplyVertical}
              onCancel={onCancelVertical}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              radioVal={measure_val}
              setRadioVal={set_measure_val}
              onApply={onApplyMeasure}
              onCancel={onCancelMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
              radioVal={range_val}
              setRadioVal={set_range_val}
              onApply={onApplyRange}
              onCancel={onCancelRange}
              initialValue={range_initial_value}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              id="hyperscaler"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionHyperscaler}
              radioVal={hyperscaler_val}
              setRadioVal={set_hyperscaler_val}
              onApply={onApplyHyperscaler}
              onCancel={onCancelHyperscaler}
              initialValue={hyperscaler_initial_value}
            />
            <Separator height='39px' />
            <ShowTopBottomDD
              id="showtopbottom"
              wide={width >= 3000 ? true : false}
              narrow={width < 3000 ? true : false}
              showTopBottomVal={showTopBottomVal}
              setTopBottomVal={setTopBottomVal}
              onApply={onApplyShowTopBottom}
              onCancel={onCancelShowTopBottom}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              visibleInModal
              checkVal={region_val}
              setCheckVal={set_region_val}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro2"
              options={optionMetro}
              visibleInModal
              checkVal={metro_val}
              setCheckVal={set_metro_val}
            />
            <RadioDD
              title="Vertical"
              id="vertical2"
              options={optionVertical2}
              visibleInModal
              radioVal={vertical_val}
              setRadioVal={set_vertical_val}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              visibleInModal
              radioVal={measure_val}
              setRadioVal={set_measure_val}
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
              radioVal={range_val}
              setRadioVal={set_range_val}
            />
            <ResponsiveRadioDD
              title="Hyper Scaler"
              id="hyperscaler2"
              options={optionHyperscaler}
              visibleInModal
              radioVal={hyperscaler_val}
              setRadioVal={set_hyperscaler_val}
            />
            {/* <ShowTopBottomDD
              title="Show Top/Bottom"
              id="showtopbottom2"
              visibleInModal
              showTopBottomVal={showTopBottomVal}
              setTopBottomVal={setTopBottomVal}
            /> */}
          </FilterModal>
        </FilterContainer>
        <CustomerContent
          customerData={customerData}
          modalShow={modalShow}
          modalData={modalData}
          setModalShow={setModalShow}
          setModalData={setModalData}
          setReferenceID={setReferenceID}
          measure_val_onApply = {measure_val_onApply}
          range_val_onApply = {range_val_onApply}
          vertical_val_onApply = {vertical_val_onApply}
          hyperscaler_val_onApply = {hyperscaler_val_onApply}
          showTopBottomVal_onApply = {showTopBottomVal_onApply}
          region_val_onApply = {region_val_onApply}
          metro_val_onApply = {metro_val_onApply}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Customer;
