import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import { roundUpToTenth, convertToUSD } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);

function BubbleChart(props) {
  const { bubbleSelection, data, id, legend, totalRevenue } = props;

  // if (data) {
  //   data.map(element => {
  //     if (element.y_to_y_usd_growth < 0) {
  //       if (element.region === 'APAC') {
  //         element.color = 'rgba(205, 80, 60, 0.2)'
  //       } else if (element.region === 'AMER') {
  //         element.color = 'rgba(220, 116, 49, 0.2)'
  //       } else if (element.region === 'EMEA') {
  //         element.color = 'rgba(240, 204, 113, 0.2)'
  //       }
  //     }
  //   });
  //   // element.y_to_y_usd_growth < 0 || element.y_by_y_growth_percent
  // }

  useLayoutEffect(() => {
    var chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = -10;
    chart.paddingTop = 30;
    chart.paddingBottom = 0;

    chart.data = data;

    var xAxis = chart.xAxes.push(new am4charts.ValueAxis());
    xAxis.renderer.minGridDistance = 50;
    xAxis.renderer.grid.template.location = 0;
    xAxis.renderer.grid.template.strokeWidth = 0.5;
    xAxis.renderer.minGridDistance = 80;
    xAxis.id = "myCategoryAxis";

    var yAxis = chart.yAxes.push(new am4charts.ValueAxis());
    yAxis.renderer.minGridDistance = 50;
    yAxis.renderer.grid.template.location = 0;
    yAxis.renderer.grid.template.strokeWidth = 0.5;

    yAxis.renderer.minGridDistance = 80;
    yAxis.id = "myValueAxis";

    var series = chart.series.push(new am4charts.LineSeries());
    if (bubbleSelection === 'y-y_growth') {
      series.dataFields.valueX = "total_revenue";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "y_to_y_usd_growth";
    } else {
      series.dataFields.valueX = "y_to_y_usd_growth";
      series.dataFields.valueY = "y_by_y_growth_percent";
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      series.dataFields.value = "total_revenue";
    }


    series.strokeOpacity = 0;
    series.sequencedInterpolation = true;
    series.tooltip.pointerOrientation = "vertical";

    function createSeries(opacity) {
      var bullet = series.bullets.push(new am4charts.CircleBullet());
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.##')}%[/]\n[#fff]Revenue:[/] [#fff font-weight:bold]${total_revenue}[/]";
      bullet.propertyFields.fill = "color";
      bullet.strokeOpacity = 0;
      bullet.strokeWidth = 2;
      bullet.opacity = opacity;
      bullet.hiddenState.properties.opacity = 0;

      series.heatRules.push({
        target: bullet.circle,
        min: 15,
        max: 60,
        property: "radius"
      });
    }

    var seriesArr = [];
    data.map(item => {
      console.log(item)
      seriesArr.push(createSeries(item.opacity))
    })

    const bulletLabel = series.bullets.push(new am4charts.LabelBullet());
    bulletLabel.fill = am4core.color('#ffffff');
    bulletLabel.adapter.add('dy', (dy, bullet) => {
      let radiusDy = dy;
      bullet.dataItem.bullets.iterator()(firstBullet => {
        const circleBullet = firstBullet[1];
        if (bullet.dataItem.dataContext.bubbleLabelText) {
          radiusDy = circleBullet.circle.radius;
          if (bullet.dataItem.values.valueY.value >= 10) {
            radiusDy += 12;
          } else {
            radiusDy = -1 * radiusDy - 12;
          }
        }
      })
      return radiusDy;
    });
    bulletLabel.label.adapter.add("text", (label, target, key) => {
      return target.dataItem.dataContext.segment;
    });

    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [bubbleSelection, data, id, legend, totalRevenue]);

  return (
    <div id={id} className="bubble-chart" ></div>
  );
}

export default BubbleChart;

