import numbro from 'numbro';

export const convertToUSD = (revenue) => {
  let rev = numbro(revenue).format({thousandSeparated: false});
  let convertedNumber = rev <= -1000000 || rev >= 1000000 ? `${numbro(Math.round(Math.abs(rev / 1.0e+6 ) * 100) / 100).format({thousandSeparated: true})}M` : numbro(Math.abs(Math.round(rev))).format({thousandSeparated: true});
  return isNaN(rev) === false ? `${rev < 0 ? `-`: ''}$${convertedNumber}` : '-';
};

export const convertToMnumber = (revenue) => {
  let rev = numbro(revenue).format({thousandSeparated: false});
  return isNaN(rev) === false ? rev <= -1000000 || rev >= 1000000 ? `${numbro(Math.round((rev / 1.0e+6) * 100) / 100).format({thousandSeparated: true})}M` : numbro(Math.round(rev)).format({thousandSeparated: true}) : '-';
};

export const convertToUSDGrowth = (revenue) => {
  let value = Math.round(parseFloat(revenue.replace(/,/g, '')));
  return isNaN(value) === false ? `${value < 0 ? `-`: ''}$${numbro(Math.abs(value)).format({thousandSeparated: true})}` : '-';
}

export const convertToPPvalue = (pp_value) => {
  return `${numbro(Math.abs(pp_value)).format({ mantissa: 1 })}pp`;
};

export const convertToPercent = (percent) => {
  return `${numbro(Math.abs(percent)).format({ mantissa: 1 })}%`;
};

export const convertToWholePercent = (percent) => {
  return numbro(parseFloat(percent)).format({ mantissa: 1 });
};

export const roundUpToTenth = (value) => {
    return Math.round(parseFloat(value) * 10) / 10;
};
