const BuilderModal = (props) => {
  const { showBuilderModal, onCancelModal } = props;


  const onCancel = async () => {
    await onCancelModal()
  }

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // basic responsive for bublbe values
  const product_initial_value = productOption ? productOption[0].key : EMPTY_INITIAL;
  const [productSelection, setProductSelection] = useState(product_initial_value);

  //arrange alphabetically
  step1List.sort(function (a, b) {
    if (a.label < b.label) { return -1; }
    if (a.label > b.label) { return 1; }
    return 0;
  })

  return (
    <>
      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="product-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">

          <AnimatePresence exitBeforeEnter>
            <ContentContainer
              modalTitle='CHOOSE MODULE TO ADD'
              width={width}
            >

              <Modal.Body>
                <div className="product-container">
                  {step1List && step1List.map((item) => {
                    return (
                      <div className="btn-container" key={item.key}>
                        <Button
                          variant="link"
                          id={item.key}
                        >
                          {item.icon}{item.label}
                        </Button>
                      </div>
                    )
                  })}
                </div>
              </Modal.Body>
              <div className="filters">
                <ResponsiveBasicDD
                  title="All Categories"
                  id="all_categories"
                  addTitleOnWide
                  wide={width >= 500 ? true : false}
                  narrow={width < 500 ? true : false}
                  value={productSelection}
                  setValue={setProductSelection}
                  options={productOption}
                />
              </div>
              <Modal.Footer>
                <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                <Button variant="primary">Next</Button>
              </Modal.Footer>
            </ContentContainer>
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  modalTitle,
  children, width
}) => {
  return (
    <motion.div className={`content-container`}
      animate='visible'
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default BuilderModal;