import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import SparklineCustom from '../../components/chart/SparklineCustom';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';
import { convertToUSDGrowth, convertToPercent, convertToUSD } from '../../constants/formatter';

import './customerRevenue.scss';

const CustomerModal = (props) => {
  const data = props.data;
  const selectedCustomer = props.customer;

  let overall,
    table_details,
    revenue_mix,
    customer, deployment_map;
  if (data) {
    overall = data.overall;
    table_details = data.vertical_details || data.metro_details || data.product_details;
    revenue_mix = data.vertical_revenue_mix || data.metro_revenue_mix || data.product_revenue_mix;
    deployment_map = data.deployment_map
    customer = data.customer;
    console.log(data.vertical_details)
  }

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSD(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />

    );
  }
  const metroColumn = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt={cell.name} />
        <span className="body-name">{cell.name}</span>
      </>

    )
  }

  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'mrr_mix_percent',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metroColumn
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mrr_mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'sparkline_data',
      text: '',
      align: 'center',
      headerAlign: 'center',
      classes: 'sparkline-td',
      headerClasses: 'sparkline-td',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_customer_total',
      text: '% CUSTOMER TOTAL',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'percent_metro_total',
      text: '% METRO TOTAL',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    }
  ]

  return (
    <Modal id="customer-modal" className="customer-modal"
      {...props}
      // size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {selectedCustomer} customer revenue
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col className="fix-width" style={{ paddingRight: "30px" }}>
                <Kpi
                  data={overall}
                  total={overall.revenue}
                  withPercent={true}
                />
              </Col>
              <Col md={6} xs={12}>
                <Legend legend={deployment_map.legend} />
                <div style={{ height: "350px ", paddingTop: "2rem" }}>
                  <MapModalChart
                    id={"customer_revenue_map_chart_modal"}
                    largerWidth={false}
                    data={deployment_map.data}
                  />
                </div>
              </Col>
              <Col style={{ paddingLeft: "30px" }}>
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"customer_revenue_metro_modal_donut_chart"}
                    totalTitle={"CUSTOMER REVENUE"}
                    total={revenue_mix.customer_revenue}
                    data={revenue_mix.data}
                    legend={revenue_mix.legend}
                    isDollar={true}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <CustomTable
                  tableName='vertical-details scrollable'
                  data={table_details === undefined ? [] : table_details}
                  defaultSorted={defaultSorted} // For Initial Sort ===
                  columns={columns}
                />
              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default CustomerModal;