import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import MapChart from '../../components/chart/MapChart';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import sampleFlagImage from '../../assets/images/sample-flag.png';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';

const DeploymentContent = (props) => {
  const { deploymentData } = props;
  const overall = deploymentData.overall;
  const counts = deploymentData.overall.counts;
  const key = "counts";
  delete overall[key];

  console.log(counts);
  // Start Table
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatToPercent = (cell, row) => {
    return (
      <span>
          {convertToPercent(cell)}
      </span>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'mrc',
      text: 'MRC',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MRC MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    }
  ]
  // End Table

  // Start Table
  const barchart = (cell, row) => {
    return (
      <StackedColumnBar data={cell} />
    )
  }

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt="flag" />
        <span className="body-name">{cell.name}</span>
      </>

    )
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;
    // for (const key in by_rank_id) {
    //   const element = by_rank_id[key];
    //   if (element._id === row._id) {
    //     modalDataByRank = element
    //   }
    // }
    // if (isSelect) {
    //   setModalShow(true);
    //   setReferenceID(row._id);
    //   setModalData(modalDataByRank);
    // }
  }

  const selectRowMetro = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }

  const defaultSortedMetro = [
    {
      dataField: 'mix_percent',
      order: 'desc', // or asc
    }
  ];

  const metroColumns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment',
      headerClasses: 'segment',
      formatter: metro
    },
    // {
    //   dataField: 'stacked_column_bar_data',
    //   text: '',
    //   align: 'center',
    //   headerAlign: 'center',
    //   classes: 'sparkline-td',
    //   headerClasses: 'sparkline-td',
    //   formatter: barchart
    // },
    {
      dataField: 'billing_mrc',
      text: 'BILLING MRC',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },

    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: indicatorPercent,
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    }
  ]
  // End Table
  return (
    <Container className="deployment" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>billing mrc</Card.Title>
              <Kpi data={overall}
                total={overall.total}
                withPercent={true} />
              {counts ?
                <>
                  <div className=" kpi-item border-top">
                    {counts.slice(0, 2).map((item, i) => {
                      return (
                        <div className="kpi-content">
                          <div className="title">{item.label}</div>
                          <div className="val">{item.value}</div>
                        </div>
                      )
                    })}
                  </div>

                  <div className="kpi-item  border-top">
                    {counts.slice(Math.max(counts.length - 2, 0)).map((item, i) => {
                      return (
                        <div className="kpi-content">
                          <div className="title">{item.label}</div>
                          <div className="val">{item.value}</div>
                        </div>
                      )
                    })}
                  </div>
                </>
                : null}
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>billing mrc by region</Card.Title>
              <Legend legend={deploymentData.billing_mrc_by_region.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="total_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.billing_mrc_by_region.data}
                  legend={deploymentData.billing_mrc_by_region.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>billing mrc mix</Card.Title>
              <Legend legend={deploymentData.billing_mrc_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"billing_mrc_donut_chart"}
                  totalTitle={"BILLING MRC"}
                  total={deploymentData.billing_mrc_mix.customer_revenue}
                  data={deploymentData.billing_mrc_mix.data}
                  legend={deploymentData.billing_mrc_mix.legend}
                  isDollar={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details'
                data={deploymentData.segment_details === undefined ? [] : deploymentData.segment_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={deploymentData.segment_trends.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={deploymentData.segment_trends.data}
                  legend={deploymentData.segment_trends.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>no. of metros</Card.Title>
              <Legend legend={deploymentData.no_of_metros_by_region.legend} />
              <div style={{ height: "300px" }}>
                <StackedColumnChart
                  id="number_of_metros_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.no_of_metros_by_region.data}
                  legend={deploymentData.no_of_metros_by_region.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>no. of cabinets</Card.Title>
              <Legend legend={deploymentData.no_of_cabs_by_region.legend} />
              <div style={{ height: "300px" }}>
                <StackedColumnChart
                  id="number_of_cabinets_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.no_of_cabs_by_region.data}
                  legend={deploymentData.no_of_cabs_by_region.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>total kva</Card.Title>
              <Legend legend={deploymentData.total_kva_by_region.legend} />
              <div style={{ height: "300px" }}>
                <StackedColumnChart
                  id="total_kva_by_region_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.total_kva_by_region.data}
                  legend={deploymentData.total_kva_by_region.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>no. of interconnections</Card.Title>
              <Legend legend={deploymentData.no_of_ic_by_region.legend} />
              <div style={{ height: "300px" }}>
                <StackedColumnChart
                  id="number_of_interconnections_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.no_of_ic_by_region.data}
                  legend={deploymentData.no_of_ic_by_region.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      {/* <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>total kva</Card.Title>
              <Legend legend={deploymentData.billing_waterfall.legend} />
              <div style={{ height: "500px" }}>
                <StackedColumnChart
                  id="billing_waterfall_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={deploymentData.billing_waterfall.data}
                  legend={deploymentData.billing_waterfall.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row> */}
      <Row className="contentDivider">
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={deploymentData.deployment_map.legend} />
              <div style={{ height: "420px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"deployment_map_chart"}
                  largerWidth={true}
                  data={deploymentData.deployment_map.data}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={12} xl={6} sm={12}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>metro ranking</Card.Title>
              <CustomTable
                tableName='metro-ranking scrollable'
                data={deploymentData.metro_ranking.by_rank.data === undefined ? [] : deploymentData.metro_ranking.by_rank.data}
                selectRow={selectRowMetro} // For Selecting Row ===
                defaultSorted={defaultSortedMetro} // For Initial Sort ===
                rowClasses={rowClasses}
                columns={metroColumns}
              />
              <Table responsive className="separate-table">
                <tbody>
                  {
                    deploymentData.metro_ranking.by_top_rank.map((data, i) => {
                      return (
                        <tr key={i} className={i === 0 ? 'border-head height1' : 'height1'}>
                          <td className="rank"></td>
                          <td className="metro">{data.metro}</td>
                          {/* <td className="sparkline-td">
                            <StackedColumnBar data={data.stacked_column_bar_data} />
                          </td> */}
                          <td className="text-right" >{formatUSDGrowth(data.billing_mrc)}</td>
                          <td className="text-right">{formatToPercent(data.mix_percent)}</td>
                          <td className="text-right">
                            <span className="indicator indicator-up " style={{ top: "70.7%" }}>
                              <ImArrowUpRight2 />
                              {data.y_by_y_growth_percent}
                            </span></td>
                          <td className="text-right">{formatUSDGrowth(data.y_to_y_usd_growth)}</td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>

            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default DeploymentContent;