import React, { useState } from 'react';
import { Dropdown, Form, Button, FormControl } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const ShowTopBottomDD = ({ options, className, id, title, visibleInModal, showTopBottomVal, setTopBottomVal, onApply, onCancel }) => {
  const [show, setShow] = useState(false);

  const onChangeShowRadio = (e) => {
    setTopBottomVal({
      ...showTopBottomVal,
      [e.target.name]: e.target.id
    })
  }

  const onChangeShowInput = (e) => {
    setTopBottomVal({
      ...showTopBottomVal,
      numberInput: e.target.value >= 0 ? e.target.value : 0,
    })
  }

  const handleToggle = () => {
    setShow(!show)
    onCancel();
  }

  const content = <>
    <Form.Check
      custom
      type='radio'
      name='topBottom'
      label='Top'
      id='radio_top'
      checked={showTopBottomVal.topBottom === 'radio_top'}
      onChange={(e) => onChangeShowRadio(e)}
    />
    <Form.Check
      custom
      type='radio'
      name='topBottom'
      label='Bottom'
      id='radio_bottom'
      checked={showTopBottomVal.topBottom === 'radio_bottom'}
      onChange={(e) => onChangeShowRadio(e)}
    />

    <FormControl
      type="number"
      min="0"
      placeholder="0"
      name="numberInput"
      value={showTopBottomVal.numberInput}
      onChange={onChangeShowInput}
    />

    <Form.Check
      custom
      type='radio'
      name='rankingPercentile'
      label='Ranking'
      id='radio_ranking'
      checked={showTopBottomVal.rankingPercentile === 'radio_ranking'}
      onChange={(e) => onChangeShowRadio(e)}
    />
    <Form.Check
      custom
      type='radio'
      name='rankingPercentile'
      label='Percentile'
      id='radio_percentile'
      checked={showTopBottomVal.rankingPercentile === 'radio_percentile'}
      onChange={(e) => onChangeShowRadio(e)}
    />
  </>

  return (
    <>
      {visibleInModal
        ? <div className={`radio-in-modal narrow-list filter-modal ${className ? className : ''}`}>
            <div className="title">Show Top / Bottom</div>
            {content}
          </div>
        : <Dropdown
            show={show}
            onToggle={handleToggle}
            className={`show-top-bottom-dd narrow-list ${className ? className : ''}`}
          >
            <Dropdown.Toggle id={`radio_dd-${id}`} variant='light' className={ showTopBottomVal.numberInput !== 0 ? 'hasPick' : ''}>
              Show Top / Bottom <BiChevronDown viewBox="6 8 12 12" />
            </Dropdown.Toggle>

            <Dropdown.Menu show={show}>
              {content}
              <div className="btn-block">
                <Button variant="link" onClick={handleToggle}>Cancel</Button>
                <Button variant="primary" onClick={() => {
                    onApply(); 
                    setShow(!show);
                }}>Apply</Button>
              </div>
            </Dropdown.Menu>
          </Dropdown>
      }
    </>
  );
}

export default ShowTopBottomDD;
