import React from 'react';
import { Dropdown } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi';
import './customDropdown.scss';


const ResponsiveBasicDD = ({ 
  options, 
  wide, 
  narrow, 
  className, 
  id, 
  selection,
  initialValue,
  value,
  setValue 
}) => {
  const handleSelect = (key) => {
    setValue(key)
    selection && selection(key)
  }

  function humanize(str) {
    if (str.indexOf('_') > -1){
      var i, frags = str.split('_');
      for (i=0; i<frags.length; i++) {
        frags[i] = frags[i].charAt(0).toUpperCase() + frags[i].slice(1);
      }
      return frags.join(' ');
    } else{
      return str.charAt(0).toUpperCase() + str.slice(1);
    }
  }

  return (
    <>
      {wide &&
        <ul className={`responsive-basic-dd wide-list ${className ? className : ''}`}>
          {options
            ? <>
              {options.map(option => (
                <li key={option.key}
                  onClick={() => handleSelect(option.key)}
                  className={`${option.key === value ? 'active' : ''}`}
                >
                  {option.text}
                </li>
              ))}
            </>
            : <li>{initialValue}</li>
          }
        </ul>
      }
      {narrow &&
        <Dropdown className={`responsive-basic-dd narrow-list ${className ? className : ''}`}>
          <Dropdown.Toggle id={`responsive_basic_dd-${id}`} variant='light'>
            {value ? humanize(value) : ''} <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu>
            {options && options.map(option => (
              <Dropdown.Item
                key={option.key}
                eventKey={option.key}
                onClick={() => handleSelect(option.key)}
              >
                {option.text}
              </Dropdown.Item>
            ))}
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default ResponsiveBasicDD;
