import React, {useState} from 'react';
import { Modal, Button } from 'react-bootstrap';

import RadioDD from '../../components/customDropdown/RadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';


const metricCategory = [
  {
    key: 'topline',
    text: 'Topline'
  },
  {
    key: 'platform',
    text: 'Platform'
  },
  {
    key: 'market',
    text: 'Market'
  },
  {
    key: 'customer',
    text: 'Customer'
  },
  {
    key: 'product',
    text: 'Product'
  },
  {
    key: 'operation',
    text: 'Operation'
  }
]

const AddMetricsModal = (props) => {
  const data = props.data;
  const setContent= props.setContent;

  // radio for metric values
  const metric_initial_value = metricCategory ? metricCategory[0].key : ''
  const [metric_val_onApply, set_metric_val_onApply] = useState(metric_initial_value);
  const [metric_val, set_metric_val] = useState(metric_initial_value);
  const onApplyMetric = () => {
    set_metric_val_onApply(metric_val)
  }
  const onCancelMetric = () => {
    set_metric_val(metric_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  return (
    <Modal id="metrics-modal" className="metrics-modal"
      {...props}
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header>
        <Modal.Title id="contained-modal-title-vcenter">
          choose metrics to add
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="custom-modal">
        <RadioDD
          title="Metric Category"
          id="metric-category"
          options={metricCategory}
          radioVal={metric_val}
          setRadioVal={set_metric_val}
          onApply={onApplyMetric}
          onCancel={onCancelMetric}
        />
        <SearchCheckboxDD
          title="Metro"
          id="metro"
          visibleInModal
          options={[
            {
              key: 'total_revenue',
              text: 'Total Revenue'
            },
            {
              key: 'customer_revenue',
              text: 'Customer Revenue'
            },
            {
              key: 'gross-bookings',
              text: 'Gross Bookings'
            },
            {
              key: 'net-bookings',
              text: 'Net Bookings'
            },
            {
              key: 'cross-border-trade',
              text: 'Cross Border Trade'
            },
            {
              key: 'pricing',
              text: 'Pricing'
            },
            {
              key: 'churn',
              text: 'Churn'
            },
            {
              key: 'new-logos',
              text: 'New Logos'
            },
            {
              key: 'logo-extension',
              text: 'Logo Extension'
            },
            {
              key: 'sales-productivity',
              text: 'Sales Productivity'
            },
            {
              key: 'ebitda',
              text: 'EBITDA'
            }
          ]}
          checkVal={metro_val}
          setCheckVal={set_metro_val}
          onApply={onApplyMetro}
          onCancel={onCancelMetro}
        />

      </Modal.Body>
      <div className="btn-container">
        <Button variant="link" onClick='' size="sm">Cancel</Button>
        <Button variant="primary" size="sm" onClick={() => {
          setContent(true)
        }}>Next</Button>
      </div>

    </Modal>
  );
}

export default AddMetricsModal;