import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import HorizontalFunnelChart from '../../components/chart/HorizontalFunnelChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';

const OrdersContent = (props) => {
  const { ordersData } = props;

  // Start Table
  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'mrr',
      text: 'BKGS',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorDown,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q-Q $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_budget',
      text: '%BUDGET',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_mpq',
      text: '%MPQ',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  // End Table

  return (
    <Container className="orders-tab" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>open orders</Card.Title>
              <Kpi data={ordersData.overall}
                total={ordersData.overall.total}
                withPercent={true} />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>

              <Card.Title>open orders by stage</Card.Title>
              <Row>
                {ordersData.open_orders_by_stage.data && ordersData.open_orders_by_stage.data.map((item) => {
                  return (
                    <Col>
                      <Row>

                        <Col>
                          <div className="funnel-label-container text-right">
                            <div className="header">
                              {item.segment}
                            </div>
                            <div className="value">
                              <span>{item.order_count}</span>
                    </div>
                          </div>
                        </Col>
                        <Col>
                          <div className="funnel-label-container">
                            Avg Days
                            <div className="indicator indicator-down">
                              {item.avg_days} ({item.avg_days_delta})
                  </div>
                            <div className="quarter">
                              vs {item.quarter_period}
                            </div>
                          </div>
                        </Col>

                      </Row>

                    </Col>
                  )
                })}
              </Row>
              <div style={{ height: "300px" }}>
                <HorizontalFunnelChart id="open_orders_by_stage_chart"
                  data={ordersData.open_orders_by_stage.data}
                  legend={ordersData.open_orders_by_stage.legend} />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>open order count mix</Card.Title>
              <Legend legend={ordersData.open_orders_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"total_revenue_overview_donut_chart"}
                  totalTitle={"OPEN ORDERS"}
                  total={ordersData.open_orders_mix.open_orders}
                  isDollar={false}
                  data={ordersData.open_orders_mix.data}
                  legend={ordersData.open_orders_mix.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        {ordersData.stages && ordersData.stages.map((item) => {
          return (
            <Col md={3} lg={3}>
              <Card className="bg-gray">
                <Card.Body>
                  <Card.Title>{item.title}</Card.Title>
                  {item.cards && item.cards.map((cardItem) => {
                    return (
                      <Card className="account-card">
                        <Card.Body>
                          <div className="number">
                            {cardItem.order_id}
                          </div>
                          <div className="title">
                            {cardItem.account_name}
                          </div>
                          <div className="tag-container">
                            <span className="tag region">{cardItem.region}</span>
                            <span className="tag metro">{cardItem.metro}</span>
                          </div>
                          <div className="total">
                            Total MRC: <span>{cardItem.total_mrc}</span>
                          </div>
                          <div className="total">
                            Total NRC: <span>{cardItem.total_nrc}</span>
                          </div>
                          <div className="days">
                            Days in Stage: 
                            {cardItem.dis_indicator === 'red' ? 
                             <span className="indicator indicator-down"><ImArrowDownRight2 /> {cardItem.days_in_stage}</span>
                             :
                             <span className="indicator indicator-up"><ImArrowUpRight2 /> {cardItem.days_in_stage}</span>
                            }
                          </div>
                          <div className="days">
                            Days since creation: 
                            {cardItem.dsc_indicator === 'red' ? 
                             <span className="indicator indicator-down"><ImArrowDownRight2 /> {cardItem.days_since_creation}</span>
                             :
                             <span className="indicator indicator-up"><ImArrowUpRight2 /> {cardItem.days_since_creation}</span>
                            }
                          </div>
                        </Card.Body>
                      </Card>
                    )
                  })}
                </Card.Body>
              </Card>
            </Col>
          )
        })}
      </Row>

    </Container >
  );
};

export default OrdersContent;