import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Button, ButtonGroup } from 'react-bootstrap';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionAccounts, optionProspect, optionMeasure, optionRange, optionRegion, optionMetro, EMPTY_INITIAL } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import RevenueContent from './RevenueContent';
import BookingsContent from './BookingsContent';
import OrdersContent from './OrdersContent';
import OpportunitiesContent from './OpportunitiesContent';

const Financials = () => {
  const [revenueData, setRevenueData] = useState([])
  const [bookingsData, setBookingsData] = useState([]);
  const [ordersData, setOrdersData] = useState([]);
  const [opportunitiesData, setOpportunitiesData] = useState([]);
  const [isLoading, setIsLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('revenue');

  // Axios call
  const fetchData = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/financials/revenue.json?print=pretty`,
    }).then(result => {
      setRevenueData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
   await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/financials/bookings.json?print=pretty`,
    }).then(result => {
      setBookingsData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/financials/orders.json?print=pretty`,
    }).then(result => {
      setOrdersData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
   await  axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/financials/opportunities.json?print=pretty`,
    }).then(result => {
      setOpportunitiesData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val)
  }
  const onCancelRegion = () => {
    set_region_val(region_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0].key : ''
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio responsive for range values
  const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio responsive for accounts values
  const accounts_initial_value = optionAccounts ? optionAccounts[0].key : EMPTY_INITIAL
  const [accounts_val_onApply, set_accounts_val_onApply] = useState(accounts_initial_value);
  const [accounts_val, set_accounts_val] = useState(accounts_initial_value);
  const onApplyAccounts = () => {
    set_accounts_val_onApply(accounts_val)
  }
  const onCancelAccounts = () => {
    set_accounts_val(accounts_val_onApply)
  }

  // radio responsive for prospects values
  const prospects_initial_value = optionProspect ? optionProspect[0].key : EMPTY_INITIAL
  const [prospects_val_onApply, set_prospects_val_onApply] = useState(prospects_initial_value);
  const [prospects_val, set_prospects_val] = useState(prospects_initial_value);
  const onApplyProspects = () => {
    set_prospects_val_onApply(prospects_val)
  }
  const onCancelProspects = () => {
    set_prospects_val(prospects_val_onApply)
  }

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <ButtonGroup className="sub-tab">
              <Button 
                variant='light' 
                className={activeTab === 'revenue' ? 'active' : ''}
                onClick={() => {setActiveTab('revenue')}}>
                Revenue
              </Button>
              <Button 
                variant='light' 
                className={activeTab === 'bookings' ? 'active' : ''}
                onClick={() => {setActiveTab('bookings')}}>
                Bookings
              </Button>
              <Button 
                variant='light' 
                className={activeTab === 'orders' ? 'active' : ''}
                onClick={() => {setActiveTab('orders')}}>
                Orders
              </Button>
              <Button 
                variant='light' 
                className={activeTab === 'opportunities' ? 'active' : ''}
                onClick={() => {setActiveTab('opportunities')}}>
                Opportunities
              </Button>
            </ButtonGroup>
            <Separator height='39px' />
            <CheckboxDD
              title="Region"
              id="region"
              options={optionRegion}
              checkVal={region_val}
              setCheckVal={set_region_val}
              onApply={onApplyRegion}
              onCancel={onCancelRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              checkVal={metro_val}
              setCheckVal={set_metro_val}
              onApply={onApplyMetro}
              onCancel={onCancelMetro}
            />
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
              radioVal={measure_val}
              setRadioVal={set_measure_val}
              onApply={onApplyMeasure}
              onCancel={onCancelMeasure}
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
              radioVal={range_val}
              setRadioVal={set_range_val}
              onApply={onApplyRange}
              onCancel={onCancelRange}
              initialValue={range_initial_value}
            />
          </div>
          <FilterModal
            title="Navigator"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <ResponsiveRadioDD
              id="accounts_modal"
              options={optionAccounts}
              visibleInModal
              radioVal={accounts_val}
              setRadioVal={set_accounts_val}
            />
            <ResponsiveRadioDD
              id="include_only_modal"
              options={optionProspect}
              visibleInModal
              radioVal={prospects_val}
              setRadioVal={set_prospects_val}
            />
          </FilterModal>
        </FilterContainer>
        {/* ============= Your Component here! ============= */}
        {activeTab === 'revenue' && <RevenueContent revenueData={revenueData ? revenueData[0] : []} />}
        {activeTab === 'bookings' && <BookingsContent bookingsData={bookingsData ? bookingsData[0] : []} />}
        {activeTab === 'orders' && <OrdersContent ordersData={ordersData ? ordersData[0]: []} />}
        {activeTab === 'opportunities' && <OpportunitiesContent opportunitiesData={opportunitiesData ? opportunitiesData[0] : []} />}
      </div>

      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Financials;
