/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { useWidowResize } from '../../constants/utilityHooks';
import CustomTable from '../../components/customTable/CustomTable';
//Charts
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import Legend from '../../components/legend/Legend';
import LineChart from '../../components/chart/LineChart';
import CustomerModal from './CustomerModal';
import { API_BASE } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const CustomerContent = (props) => {
  const { customerData, modalShow, modalData, setModalShow, setModalData, setReferenceID, measure_val_onApply, range_val_onApply, vertical_val_onApply, hyperscaler_val_onApply, region_val_onApply, metro_val_onApply, showTopBottomVal_onApply } = props;
  const top_customers_by_mrr = customerData.top_customers_by_mrr;
  const top_customers_by_y_on_y = customerData.top_customers_by_y_on_y;
  const deployment_map = customerData.deployment_map;
  const customer_ranking = customerData.customer_ranking;

  const [map_region_val, set_map_region_val] = useState(null);
  const [isRankingTableFiltered, setIsRankingTableFiltered] = useState(false);
  const [selectedCustomer, setCustomerSelected] = useState(null);

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />
    );
  }


  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===
  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;
    setCustomerSelected(row.customer)
    for (const key in mdalRankingById) {
      const element = mdalRankingById[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }
    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'mrr_mix_percent',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      align: 'left',
      headerAlign: 'left',
      classes: 'customer',
      headerClasses: 'customer',
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mrr_mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'sparkline_data',
      text: '',
      align: 'center',
      headerAlign: 'center',
      classes: 'sparkline-td',
      headerClasses: 'sparkline-td',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_total_usd_growth',
      text: '% TOTAL $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    }
  ]

  const [isLoading, setIsLoading]=useState(true);
  const [tableData, setTableData] = useState([]);
  const [mdalRankingById, setModalRankingById] = useState([]);

  const onRankingTableFiltered = () => {
    const token = localStorage.getItem('token');
    let url = `${API_BASE}/orbis/api/customerrevenuecustomer?measure=${measure_val_onApply}&range=${range_val_onApply}&vertical=${vertical_val_onApply}&hyperscaler=${hyperscaler_val_onApply}&show=${showTopBottomVal_onApply.topBottom.split('_')[1]}&showvalue=${showTopBottomVal_onApply.numberInput}${region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : ''}${metro_val_onApply.length > 0 ? `&rmetro=${metro_val_onApply.join(",").toString()}` : ''}`;
    setIsLoading(true);
  
    if (isRankingTableFiltered === true)  {
      axios({
        method: 'GET',
        url: `${url}${map_region_val != null ? `&mapregion=${map_region_val}` : ''}`,
        headers: {
          'Content-Type': 'application/json',
          "Authorization": `bearer ${token}`
        }
      }).then(result => {
        let customer_ranking_table = result.data.customer_ranking;
        setIsLoading(false);
        setTableData(customer_ranking_table.by_rank.data);
        setModalRankingById(customer_ranking_table.by_rank_id);
      }).catch(error => {
        setIsLoading(false);
        console.log(error);
        setTableData([]);
      });
     
    } else {
      setIsLoading(false);
      setTableData(customer_ranking.by_rank.data);
      setModalRankingById(customer_ranking.by_rank_id);
    }
  };

  useEffect(() => {
    onRankingTableFiltered();
  }, [map_region_val]);

  return (
    <Container fluid>
      <CustomerModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
        customer={selectedCustomer}
      />
      <Row className="contentDivider">
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>{showTopBottomVal_onApply.topBottom.split('_')[1]} {showTopBottomVal_onApply.numberInput} Customers by MRR</Card.Title>
              <Legend legend={top_customers_by_mrr.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="top_customers_by_mrr"
                  largerWidth={true}
                  isOneHundredPercent={true}
                  isPercentage={true}
                  insideLabel={true}
                  percentLabel={true}
                  hiddenValueAxis={true}
                  data={top_customers_by_mrr.data}
                  legend={top_customers_by_mrr.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>{showTopBottomVal_onApply.topBottom.split('_')[1]} {showTopBottomVal_onApply.numberInput} Customers by Y/Y Growth</Card.Title>
              <Legend legend={top_customers_by_y_on_y.legend} />
              <div style={{ height: "250px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={top_customers_by_y_on_y.data}
                  legend={top_customers_by_y_on_y.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={deployment_map.legend} />
              <div style={{ height: "450px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"customer_revenue_map_chart"}
                  largerWidth={true}
                  data={deployment_map.data}
                  setMapRegion={set_map_region_val}
                  setIsRankingTableFiltered={setIsRankingTableFiltered}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              {isLoading ? <Loader /> : 
              <>
                <Card.Title>customer ranking</Card.Title>
                <CustomTable
                  tableName='customer-ranking scrollable'
                  data={customer_ranking.by_rank.data === undefined ? [] : tableData}
                  selectRow={selectRow} // For Selecting Row ===
                  defaultSorted={defaultSorted} // For Initial Sort ===
                  rowClasses={rowClasses}
                  columns={columns}
                />
              </>}
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>

  );
};

export default CustomerContent;