import React, {useState} from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { EMPTY_INITIAL } from '../../constants/filterOptions';

import CustomTable from '../../components/customTable/CustomTable';
import BubbleChart from '../../components/chart/BubbleChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import ResponsiveBasicDD from '../../components/customDropdown/ResponsiveBasicDD';
import Legend from '../../components/legend/Legend';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';

const bubbleOption = [
  {
    key: 'rev_dollar',
    text: 'Bubble = Rev $'
  },
  {
    key: 'y-y_growth',
    text: 'Bubble = Y-Y $ Growth'
  }
]


const GrowthContent = (props) => {
  const { bubbleName, selectedData, idState, width, resultData, selectedBubbleData, setSelectedData, setSelectedBubbleData, setBubbleName, setReferenceID, growthData } = props;

  // basic responsive for bublbe values
  const bubble_initial_value = bubbleOption ? bubbleOption[0].key : EMPTY_INITIAL;
  const [bubbleSelection, setBubbleSelection] = useState(bubble_initial_value);

  const segment_ranking = growthData.segment_ranking;
  let by_rank_id = resultData ? resultData.by_rank_id : [];

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      } else {
        if (item.region === 'APAC') {
          item.color = '#CD503C'
        } else if (item.region === 'AMER') {
          item.color = '#DC7431'
        } else if (item.region === 'EMEA') {
          item.color = '#F0CC71'
        }
      }
    })
  }

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>;
  
    return (
      <>
        <span className={`indicator ${bgColor}`}> 
            {icon}
            {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
          {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
          {convertToPercent(cell)}
      </span>
    );
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let selectedData;
    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        selectedData = element
      }
    }
    if (isSelect) {
      setReferenceID(row._id);
      setSelectedData(selectedData);
      setSelectedBubbleData(growthData.total_revenue_size_and_growth.data)
      setBubbleName(row.segment)
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment',
      headerClasses: 'segment'
    },
    {
      dataField: 'total_revenue',
      text: 'TOTAL REVENUE',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },

    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'y_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'q_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatToPercent
    }
  ]
  // End Table

  return (
    <Container className="growth" fluid>
      <Row className="contentDivider">
        <Col md="5" xs="12">
          <Card className="revenue">
            <Card.Body>
              <Card.Title>total revenue size and growth</Card.Title>
              <div className="d-flex">
                <Legend legend={growthData.total_revenue_size_and_growth.legend} />
                <ResponsiveBasicDD
                  className=" ml-auto"
                  id="group"
                  addTitleOnWide
                  wide={width >= 1516 ? true : false}
                  narrow={width < 1516 ? true : false}
                  options={bubbleOption}
                  value={bubbleSelection}
                  setValue={setBubbleSelection}
                />
              </div>

              <div style={{ height: "92%" }}>
                <BubbleChart
                  id={idState ? bubbleName : bubbleSelection ? bubbleSelection : "customer_revenue_bubble_chart"}
                  data={idState ? selectedBubbleData : growthData.total_revenue_size_and_growth.data}
                  selected={idState}
                  bubbleSelection={bubbleSelection}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md="7" xs="12">
          <Row>
            <Col md="12" xs="12" style={{ paddingBottom: "0px" }}>
              <Card className="revenue ">
                <Card.Body>
                  <Card.Title>segment ranking</Card.Title>
                  <CustomTable
                    tableName='segment-ranking scrollable'
                    data={segment_ranking.by_rank === undefined ? [] : segment_ranking.by_rank}
                    selectRow={selectRow} // For Selecting Row ===
                    defaultSorted={defaultSorted} // For Initial Sort ===
                    rowClasses={rowClasses}
                    columns={columns}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col style={{ paddingTop: "30px", paddingBottom: " 0px" }} md="12" xs="12">
              <Row >
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment y/y growth trends</Card.Title>
                      <Legend legend={selectedData ? selectedData.segment_y_by_y_growth_trends.legend : growthData.segment_trends.legend} />
                      <div style={{ height: "350px" }}>
                        <LineChart
                          id={"total_revenue_growth_line_chart"}
                          data={selectedData ? selectedData.segment_y_by_y_growth_trends.data : growthData.segment_trends.data}
                          legend={selectedData ? selectedData.segment_y_by_y_growth_trends.legend : growthData.segment_trends.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card >
                </Col>
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment contribution</Card.Title>
                      <Legend legend={selectedData ? selectedData.segment_by_region.legend : growthData.segment_by_region.legend} />
                      <div style={{ height: "350px" }}>
                        <StackedColumnChart
                          id="total_revenue_segment_by_region_stacked_column_chart"
                          hiddenValueAxis={true}
                          isOneHundredPercent={false}
                          isPercentage={false}
                          largerWidth={true}
                          hideLabelBullet={false}
                          data={selectedData ? selectedData.segment_by_region.data : growthData.segment_by_region.data}
                          legend={selectedData ? selectedData.segment_by_region.legend : growthData.segment_by_region.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

export default GrowthContent;